import ExampleList from "./ExampleList"

function List(){
const texts = ["Web Dev","Mobile Dev","UI & UX disign","DevOps Eng","Custom web dev"]
const subtexts = ["Web Dev -  -  -  ","Mobile Dev -     Android  IOS","UI & UX design -    ","DevOps Eng -     ","Custom web dev -   -"]

return  (<div>
    {texts.map((text,i)=>{
return <ExampleList title={text} key={text} subtext={subtexts[i]}/>
    })}
</div>
)
}

export default List